import React from "react";

import KeyboardDoubleArrowLeftRoundedIcon from "@mui/icons-material/KeyboardDoubleArrowLeftRounded";
import Box from "@mui/material/Box";
import Grid from "@mui/material/Grid";
import TextField from "@mui/material/TextField";
import Typography from "@mui/material/Typography";
import Stack from "@mui/material/Stack";

import { Formik } from "formik";
import { get } from "lodash";
import { useGoogleReCaptcha } from "react-google-recaptcha-v3";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate, Link } from "react-router-dom";
import { toast } from "react-toastify";
import * as Yup from "yup";

import { postForgetPassword } from "../../../store/slice/auth/signin/forgetPasswordSlice";
import CMLoadingButton from "../../ui-components/buttons/CMLoadingButton";
import translate from "../../hooks/translate";

const ForgetPasswordEmailVerify = () => {
  const { executeRecaptcha } = useGoogleReCaptcha();
  const navigate = useNavigate();
  const dispatch = useDispatch();

  const loading = useSelector((state) => state.forgetPassword.loading);

  const schemaObj = Yup.object().shape({
    email: Yup.string()
      .email(translate("common_error_email_valid_email"))
      .required(translate("common_error_email_required")),
  });

  const handleForgetPassword = async (data) => {
    if (!executeRecaptcha) {
      toast.error("You are not authorized");
      return;
    }
    const token = await executeRecaptcha();
    const payload = {
      email: get(data, "email", ""),
      googleRecaptchaToken: token,
    };
    await dispatch(postForgetPassword(payload))
      .unwrap()
      .then((result) => {
        if (get(result, "status", false)) {
          toast.success(result.message);
          navigate("/forget-password-otp-verify", {
            state: { isPasswordVerified: true },
          });
        } else {
          toast.error(result.message);
        }
      })
      .catch((error) => {
        console.log("Forget Error=====>", error);
      });
  };

  return (
    <>
      <Box component="div" sx={{ width: "100%" }}>
        <Formik
          initialValues={{
            email: "",
          }}
          validationSchema={schemaObj}
          onSubmit={(values) => {
            handleForgetPassword(values);
          }}
        >
          {(props) => (
            <form onSubmit={props.handleSubmit}>
              <Grid container>
                <Grid
                  item
                  xs={12}
                  sm={12}
                  md={10}
                  lg={8}
                  sx={{ mx: { md: "0", xs: "auto" } }}
                >
                  <Typography variant="h2" align="center" mb={1}>
                    {translate("common_recover_password")}
                  </Typography>{" "}
                  <Typography variant="body1" align="center" mb={3}>
                    {translate("consultant_enter_your_recovery_email")}
                  </Typography>
                  <Grid container spacing={2} justifyContent="center">
                    <Grid item xs={12}>
                      <TextField
                        label={translate("common_email")}
                        name="email"
                        value={props.values.email}
                        onChange={props.handleChange}
                        onBlur={props.handleBlur}
                        error={
                          props.errors.email && props.touched.email
                            ? true
                            : false
                        }
                        helperText={
                          props.errors.email && props.touched.email
                            ? props.errors.email
                            : null
                        }
                      />
                    </Grid>
                    <Grid item xs={12}>
                      <CMLoadingButton
                        variant="contained"
                        fullWidth
                        type="submit"
                        loading={props.isSubmitting && loading}
                        disabled={props.isSubmitting && loading}
                      >
                        {translate("common_btn_submit")}
                      </CMLoadingButton>
                    </Grid>
                    <Stack
                      direction="row"
                      spacing={1}
                      alignItems="center"
                      my={2}
                    >
                      <KeyboardDoubleArrowLeftRoundedIcon color="primary" />
                      <Link to="/login">
                        {translate("common_back_to_login")}
                      </Link>
                    </Stack>
                  </Grid>
                </Grid>
              </Grid>
            </form>
          )}
        </Formik>
      </Box>
    </>
  );
};

export default ForgetPasswordEmailVerify;
